import React, { PureComponent } from 'react';
import '../animate.css';
import banner from '../images/background.jpg'
import image1 from '../images/1.jpg'
import image2 from '../images/2.jpg'
class Banner extends PureComponent {
    render() { 
        return ( 

            <React.Fragment>
                <div id="banner">
                    <h1 id="arts">ARTISTIC STUDIOS</h1>
                    <div id="artisticStudios">
                        <img src={banner} id="bannerMainImg"/>
                    </div>
                
                    <h1 id="alones">ALONE SEASON 4</h1>
                    <div id="alone">
                        <div id="button"><a href="http://shop.artisticstudios.co.za"><button>Shop now</button></a></div>
                        <div id="aloneCont">
                            
                            <div id="left">
                                <img src={image2} id="bannerMainImg"/>
                            </div>
                            <div id="right">
                                <img src={image1} id="bannerMainImg"/>
                            </div>
                        </div>
                    </div>

                </div>
            </React.Fragment>
         );
    }
}
 
export default Banner;